            >
          </v-avatar>
              <div class="d-flex flex-column align-center justify-around pa-3">
                <span class="white--text text-h6 ">{{ getUser.name }}</span>

                <span class="white--text text-caption">{{ getUser.role.type }} | {{ getUser.role.org }}</span>
              </div>
            </div>
