      return {
        items: [
          { title: 'Naslovnica', icon: 'mdi-view-dashboard', to: '/' },
          { title: 'Profil', icon: 'mdi-account-box' },
          { title: 'Tečajevi', icon: 'style' },
          { title: 'Izazovi', icon: 'code' },
          { title: 'Klanovi', icon: 'groups' },
          { title: 'Postavke', icon: 'settings' },
        ],
      }
    },
    computed:{
      ...mapGetters([ 'getDrawer', 'getUser'])
    }
  }
</script>