import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    drawer: null,
    user: {
      name: 'codeSUM User',
      img: 'https://cdn.shopify.com/s/files/1/2695/0984/files/logo2k2_300x300.png?v=1519094728',
      role: {
        type: 'Admin',
        org: 'FPMOZ'
      }
    }
  },
  getters: {
    getDrawer(state){
      return state.drawer
    },
    getUser(state){
      return state.user
    }
  },
  mutations: {
    CHANGE_DRAWER(state){
      state.drawer = !state.drawer
    },
    CHANGE_USER(state, payload){
      state.user = payload
    }
  },
  actions: {
    changeDrawer({ commit }){
      commit('CHANGE_DRAWER')
    },
    changeUser({ commit }, payload){
      commit('CHANGE_USER', payload)
    }
  },
  modules: {
  }
})
